* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

.appWrapper {
  display: flex;
  background-color: #0f0f0f;
  height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.appHeading {
  font-size: 35px;
  color: #1cdbf5;
  text-align: center;
}

.gameBox {
  width: 60vh;
  height: 60vh;
  background-color: white;
  display: grid;
  grid-template: repeat(3, 1fr) / repeat(3, 1fr);
  gap: 1em;
}

.square {
  background-color: #0f0f0f;
  font-size: 70px;
  text-align: center;
  color: #1cdbf5;
  font-weight: bolder;
  padding-top: 10px;
}

.fc-aqua {
  color: #1cdbf5;
}

.fc-white {
  color: white;
}
.gameContainer {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 70vh;
  align-items: center;
  justify-content: space-around;
}
.actionCenter {
  display: flex;
  height: 35vh;
  width: 50vh;
  flex-direction: column;
}
.winnerBoard {
  height: 60%;
  width: 100%;
  font-size: 50px;
  text-align: center;
  color: #1cdbf5;
  font-weight: bolder;
}
.clear {
  height: 40%;
  width: 100%;
  display: flex;
  justify-content: center;
}
.clear button {
  background-color: #1cdbf5;
  border: 0px;
  border-radius: 30px;
  height: 70px;
  width: 250px;
  font-size: 25px;
  font-weight: bold;
}
.clear button:hover {
  background-color: #0f0f0f;
  color: #1cdbf5;
}

@media only screen and (max-width: 700px) {
  .appHeading {
    font-size: 30px;
  }
  .square {
    font-size: 40px;
  }
  .gameContainer {
    display: flex;
    flex-direction: column;
  }
  .gameBox {
    height: 300px;
    width: 250px;
    gap: 0.3em;
  }
  .winnerBoard {
    padding-top: 30px;
    font-size: 30px;
  }
  .clear button {
    width: 200px;
    height: 50px;
  }
}
